import React, { useEffect, useState } from 'react'
import './style.css'

import * as logofull from '../../../assets/DULT_logo.png'
import * as google from '../../../assets/google-logo.svg'
import * as facebook from '../../../assets/facebook.svg'
import { GoogleLogin } from 'react-google-login';
import FacebookProvider, { Login } from 'react-facebook-sdk';
import { Sociallogin } from '../../../utils/actions'

import { useHistory } from 'react-router-dom';
import ReactGA from 'react-ga';

import { useAuth } from '../../../store/authStore';
import { userstore } from '../../../store/userStore';


import HeaderComponent from 'components/Common/HeaderComponent'
import FooterComponent from 'components/Common/FooterComponent'

import { useColourObjectStore } from '../../../store/ColourStore';

declare global {
  interface Window { recaptchaVerifier: any; confirmationResult: any }
}

const LoginScreen = () => {

  const history = useHistory();

  const { colourObj, loaddefaultcolor } = useColourObjectStore();

  const { auth, setAuth } = useAuth()
  const { selfuser, setselfuser } = userstore()

  const [aniout, setaniout] = useState(false);

  useEffect(() => {
    if (!auth){
    loaddefaultcolor()
  } else {
    if ((selfuser.user_type) !== "SUPER ADMIN") {
      history.push('/dashboard')
    } else {
      history.push('/')
    }
  }
    ReactGA.pageview(window.location.pathname + window.location.search);
  }, [])

  const loginCallback = async (data: any, errorresponse: any) => {
    if (data.status === 200) {
      console.log('response ' + JSON.stringify(data));
      localStorage.setItem('AuthToken', JSON.stringify(data.data.results.token));
      localStorage.setItem('UserDetails', JSON.stringify(data.data.results.user_details));
      localStorage.setItem('UsedSocialLoginMethod', "true");
      setAuth(data.data.results.token)
      setselfuser(data.data.results.user_details)

      history.push('/Dashboard')

    } else {
      console.log('error ' + JSON.stringify(data));
      console.log('error ' + JSON.stringify(errorresponse));
    };
  }

  const fade_in = (props: any) => {
    let result = (aniout ? (props + " " + "fade_out") : (props + " " + "fade_in"))
    return result
  }

  return (
    <div className={fade_in("login_page")}>

      <HeaderComponent screen={"login"} />

      <div className={fade_in("login_wrapper")}>

        <div className='login_top_container'>
          <img className='login_menu_logo' src={logofull} />
        </div>

        <div className='loginTitle_Text'>ADMIN PANNEL</div>

        <div className="login_form_wrapper">

          <div className="login_form_bg"></div>

          <div className='loginDescription_Text' style={{ textDecorationColor: colourObj.color_m2 }}>Login or Sign Up with any of the following:</div>

          <div className=" login_popupformcontainer1">

            <div className="SMlogin_button_container">

              <GoogleLogin
                clientId="581422038025-rte3a06d7kumasu887n64uikerfigmiv.apps.googleusercontent.com"
                render={renderProps => (
                  <button className="login_SMbutton" onClick={renderProps.onClick} disabled={renderProps.disabled}>
                    <img src={google} className="login_SMicon" />
                    <div className="login_buttontext1">Continue with Google</div>
                  </button>
                )}
                buttonText="Login"
                onSuccess={Response => {
                  console.log(Response)
                  var formData = new FormData()
                  let userInfo = Response.profileObj
                  formData.append('lastname', userInfo.familyName);
                  formData.append('firstname', userInfo.givenName);
                  formData.append('photo_url', userInfo.imageUrl);
                  formData.append('auth_provider', "google");
                  formData.append('email', userInfo.email);
                  formData.append('username', userInfo.googleId);
                  localStorage.setItem("Bearer", JSON.stringify(Response.accessToken))
                  Sociallogin(loginCallback, formData)
                  setaniout(true)
                  setTimeout(() => {
                    history.push("/dashboard")
                  }, 290);
                }}
                onFailure={(Response: any) => { console.log(Response) }}
                cookiePolicy={'single_host_origin'}
              />

            </div>

            <div className="SMlogin_button_container">

              <FacebookProvider appId="494516388401593">
                <Login
                  scope="email"
                  onResponse={(response: any) => {
                    console.log(response.profile)
                    console.log(Response)
                    var formData = new FormData()
                    let userInfo = response.profile
                    formData.append('lastname', userInfo.first_name);
                    formData.append('firstname', userInfo.last_name);
                    formData.append('photo_url', "");
                    formData.append('auth_provider', "fb");
                    formData.append('email', userInfo.email);
                    formData.append('username', userInfo.id);
                    Sociallogin(loginCallback, formData)
                    setaniout(true)
                    setTimeout(() => {
                      history.push("/dashboard")
                    }, 290);
                  }}
                  onError={(response: any) => {
                    console.log(response)
                  }}
                >
                  <button className="login_SMbutton">
                    <img src={facebook} className="login_SMicon" />
                    <div className="login_buttontext1">Continue with Facebook</div>
                  </button>
                </Login>
              </FacebookProvider>

            </div>

          </div>
        </div>

      </div>

      <FooterComponent />

    </div>

  );
}

export default LoginScreen;


